import React from 'react';
import { Menu } from 'antd';
import { Link } from "react-router-dom"

import Dropdown from 'components/Dropdown/Dropdown'

const Authored = props => {
  const menu = (
    <>
      <Menu.Item key="1"><Link to='/personal-data'> </Link></Menu.Item>
      <Menu.Item key="2"><Link to='/history'> </Link></Menu.Item>
      <Menu.Item key="3"><Link to='/izbrannoe'></Link></Menu.Item>
      <Menu.Item key="4"></Menu.Item>
     </> 
  )
  return (
    <Dropdown type='acc-menu' title={'  '} menuList={menu}/>
  )
}
export default Authored